<template>
  <div class="single-preview" id="single-preview">
    <p class="ui header">预览题目</p><span class="text-muted" v-if="errorMsg && question">{{errorMsg}}</span>
    <component
      v-if="question"
      :is="currentView"
      :question="question"
      :unfoldAnalysis="true"
      :key="question.uuid"></component>
    <div class="content-empty" v-if="!question && errorMsg">
      <img :src="require('assets/img/content-empty.png')">
      <p v-if="!question && errorMsg">{{errorMsg}}</p>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import Loader from 'components/loader'
import { STATE_APPROVED } from '@/question-bank/consts'
import { getModelByDkind } from '@/question-bank/utils'
import { getViewByDkind } from '@/question-bank/preview/utils'

export default {
  name: 'single-preview',
  data() {
    return {
      question: null,
      errorMsg: '',
    }
  },
  created() {
  },
  components: { Loader },
  mounted() {
    this.fetchQuestion(this.$route.params.uuid)
  },
  watch: {

  },
  computed: {
    currentView() {
      if (!this.question) return null
      return getViewByDkind(this.question.displayKind)
    },
  },
  methods: {
    fetchQuestion(uuid) {
      const url = '/question/j/questions/retrieve'
      const params = { uuids: [uuid] }
      axios.get(url, { params }).then((response) => {
        if (response.data.status === 0) {
          if (response.data.questions.length === 0) {
            this.errorMsg = '题目不存在'
            return
          }
          const questionData = response.data.questions[0]
          if (questionData.state !== STATE_APPROVED) {
            this.errorMsg = '此题未入库，本不可预览。仅供测试。'
            const QuestionModel = getModelByDkind(questionData.displayKind)
            this.question = new QuestionModel(questionData)
            return
          }
          const QuestionModel = getModelByDkind(questionData.displayKind)
          this.question = new QuestionModel(questionData)
          this.errorMsg = ''
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style lang="less">
@import "~@/question-bank/preview/index.less";
</style>

<style lang="less" scoped>
.single-preview {
  background-color: white;
  padding: 1em;
  min-height: 80vh;
  max-width: 800px;
}

</style>



