import Vue from 'vue'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import Toasted from 'vue-toasted'
import VueRouter from 'vue-router'
import SinglePreview from './single-preview'


require('./index.less')

Vue.config.productionTip = false

var axios = require('axios')

axios.defaults.headers.common['X_REQUESTED_WITH'] = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(VueRouter)
Vue.use(Toasted)

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/question-preview' : '/static/w/pages/question-preview.html',
  routes: [
    { path: '/:uuid', component: SinglePreview, name: 'preview-question' },
  ],
  linkActiveClass: 'active',
})


Vue.prototype.$eventHub = new Vue()

new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  router,
  data: {},
  components: {},
  created() {
  },
  mounted() {
    // this.$router.push({ name: 'preview-question', params: { uuid: '20180416-b91b45dfdf0b43a8b89477b2b329a4eb' }})
  },
})



